import React from "react";
import ReactDOM from "react-dom";
import { HashRouter, Route, Switch, Redirect } from "react-router-dom";
import { Provider } from "react-redux";

// Styles
// Import Flag Icons Set
import "flag-icon-css/css/flag-icon.min.css";
// Import Font Awesome Icons Set
import "font-awesome/css/font-awesome.min.css";
// Import Simple Line Icons Set
import "simple-line-icons/css/simple-line-icons.css";
// Import Main styles for this application
import "../scss/style.scss";
// Temp fix for reactstrap
import "../scss/core/_dropdown-menu-right.scss";

import "../scss/customStyle.css";

// Containers
import Login from "./containers/Login/LoginContainer";
import Dashboard from "./containers/Dashboard/Dashboard";
import PlDashboard from "./containers/PlDashboard/PlDashboard";
import Communication from "./containers/Communication/Communication";
import MutualFunds from "./containers/MutualFunds/MutualFunds";
import KafkaProd from "./containers/KafkaProd/KafkaProd";
import ManageUtils from "./containers/ManageUtils/ManageUtils";
import Gold from './containers/Gold/Gold';

//import Bureau from './containers/Bureau/Bureau';

// import msgDashboard from './containers/PlDashboard/PlDashboard';

//Views
import Page404 from "./views/Pages/Page404/";
import Page500 from "./views/Pages/Page500/";

import configureStore from "./store/configureStore";
import { PersistGate } from "redux-persist/es/integration/react";

const { persistor, store } = configureStore();
const LoadingStoreFromLocalStorage = () => {
  return <div>Please wait...</div>;
};

ReactDOM.render(
  <Provider store={store}>
    <PersistGate
      persistor={persistor}
      loading={<LoadingStoreFromLocalStorage />}
    >
      <HashRouter>
        <Switch>
          <Route exact path="/login" name="Login Page" component={Login} />
          <Route exact path="/404" name="Page 404" component={Page404} />
          <Route exact path="/500" name="Page 500" component={Page500} />
          <Route
            path="/personal-loan"
            name="PlDashboard"
            component={PlDashboard}
          />
          <Route
            path="/communication"
            name="CommBoxDashboard"
            component={Communication}
          />
          <Route
            path="/mutual-fund"
            name="MutualFundsDashboard"
            component={MutualFunds}
          />
          <Route path="/kafka" name="KafkaDashboard" component={KafkaProd} />
          <Route
            path="/manageUtils"
            name="ManageUtils"
            component={ManageUtils}
          />
          <Route
            path="/gold"
            name="Gold"
            component={Gold}
          />

          {/* <Route path="/bureau" name="Bureau" component={Bureau}/> */}
          <Route path="/" name="Dashboard" component={Dashboard} />
          <Redirect from="/" to="/login" />
        </Switch>
      </HashRouter>
    </PersistGate>
  </Provider>,
  document.getElementById("root")
);



